<section class="product-main"><div class="product-main__container"><div class="product-main__left"><div class="product-main__image"><img src="../assets/img/products/plug-big.png"></div></div><div class="product-main__right"><div class="product-main__sidebar sidebar"><div class="sidebar__name">{{ getName }}</div><div class="sidebar__body"><div class="sidebar__inner"><div class="sidebar__prop">Цена за уп.</div><div class="sidebar__value">1050 ₽</div></div><div class="sidebar__inner"><div class="sidebar__prop">Кол-во шт в упаковке</div><div class="sidebar__value">4 шт.</div></div><div class="sidebar__inner"><div class="sidebar__prop">Количество, шт</div><div class="sidebar__value sidebar__value_count"><input type="number" name="count" :value="count"><div class="minus" @click="minusCount">-</div><div class="plus" @click="plusCount">+</div></div></div></div><div class="sidebar__total"><p>Итого к оплате</p><p class="sidebar__total-price">1050 ₽</p></div><div class="sidebar__buttons"><div class="sidebar__buy">Купить</div><router-link class="sidebar__cart" to="/cart"><img src="../assets/img/icons/cart-product.svg"></router-link></div></div></div></div></section>